.header {
	background: green;
	&__container {
		display: flex;
		align-items: center; }

	h1 {
		position: absolute;
		clip: rect(0, 0, 0, 0);
		height: 1px;
		width: 1px;
		margin: -1px;
		@include hide-text; } }

.logo {
	display: block;
	&_white {
		@include s('logo'); } }

/* nav
	----------------------------------- */
.nav {
	&__list {
		display: flex;
		justify-content: space-between;
		&-item {
			border-bottom: 1px solid transparent;
			line-height: 1;
			&.is-active {
				border-bottom-color: #c9c9c9; } }
		&-link {
			@include f('pn-600', 13);
			text-transform: uppercase;
			color: $blue-dark; } } }

/* contact-info
	----------------------------------- */
.contact-info {
	&__link {
		&-icon {
			fill: $aqua;
			font-size: 20px;
			&.icon-balloon {
				font-size: 21px; }
			&.icon-mail {
				font-size: 16px; } }
		&-text {
			font-size: 15px;
			line-height: 25px;
			color: $blue-dark; } } }
