html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* COMPONENTS */
body {
  min-width: 320px;
  font-family: "ProximaNova-Regular", Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  color: #000;
}

* {
  -webkit-tap-highlight-color: transparent;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  text-decoration: none;
}

button {
  background-color: transparent;
}

h1::selection, h2::selection, h3::selection, h4::selection, h5::selection, h6::selection, p::selection, a::selection, span::selection, div::selection, img::selection, mark::selection, ul::selection, li::selection {
  color: #fff;
  background-color: #2ab1b4;
}

img {
  vertical-align: top;
  max-width: 100%;
}

::-ms-clear,
::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input:invalid {
  box-shadow: none;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

video::-webkit-media-controls-start-playback-button {
  display: none !important;
}

.obj-fit {
  width: 100%;
  height: 100%;
  object-fit: cover;
  font-family: 'object-fit: cover;';
}

.out {
  overflow: hidden;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  padding: 11px 42px;
  box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
  border-radius: 26px;
  text-transform: uppercase;
  color: #0f2740;
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 30px;
  transition: background .3s ease-in-out, color .3s ease-in-out;
}

.btn_white {
  background: #fff;
}

.no-touch .btn_white:hover {
  background: #2ab1b4;
  color: #fff;
}

.title-lg {
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 45px;
  line-height: 50px;
  text-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
  text-transform: uppercase;
  color: #fff;
}

.title-md, .title-sm {
  font-family: "ProximaNova-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 40px;
  color: #0f2740;
  line-height: 50px;
  text-transform: uppercase;
}

.title-sm {
  font-size: 30px;
  line-height: 35px;
}

.header {
  background: green;
}

.header__container {
  display: flex;
  align-items: center;
}

.header h1 {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
}

.logo {
  display: block;
}

.logo_white {
  background-image: url("../img/sprite.png");
  width: 160px;
  height: 40px;
  background-position: 0px -51px;
}

/* nav
	----------------------------------- */
.nav__list {
  display: flex;
  justify-content: space-between;
}

.nav__list-item {
  border-bottom: 1px solid transparent;
  line-height: 1;
}

.nav__list-item.is-active {
